body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px;
}

#productContainer {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  display: grid;
}

.product-card {
  background-color: #fff;
  border-radius: 8px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-card img {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  width: 100%;
  height: auto;
}

.product-card h2 {
  margin: 10px 0;
  font-size: 1.2rem;
}

.product-card p {
  margin: 10px 0;
}

.product-card div {
  margin: 5px 0;
}

.low-stock {
  color: red;
  font-family: Impact, Haettenschweiler, Arial Narrow Bold, sans-serif;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #0056b3;
}
